@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
  font-family: "Poppins", sans-serif;
}

.overall {
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  padding: 10px 15px;
  margin-top: 30px;
}

.right {
  text-align: right;
  color: rgb(88, 88, 235);
  font-size: larger;
}

.left h3 {
  color: rgb(247, 93, 93);
  display: inline;
  padding: 5px;
  font-size: 35px;
  background-color: rgba(247, 93, 93, 0.151);
}

.left p {
  font-size: larger;
  padding-top: 15px;
}

.search {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  margin-top: 30px;
}

.search input {
  width: 75%;
  padding: 20px 10px;
  font-size: larger;
  margin-top: 20px;
  border: none;
  outline: none;
  box-shadow: 0px 0px 25px rgba(124, 118, 118, 0.397);
  border-radius: 5px;
}

.search input::-webkit-input-placeholder {
  color: rgb(192, 186, 186);
}

.search input:-ms-input-placeholder {
  color: rgb(192, 186, 186);
}

.search input::placeholder {
  color: rgb(192, 186, 186);
}

.conatiners {
  width: 75%;
  display: flex;
  justify-content: space-between;
  grid-gap: 10px;
  gap: 10px;
  margin-top: 30px;
}

.conatiners div {
  display: flex;
  flex-direction: column;
  padding: 15px;
  border-radius: 5px;
}

.conatiners div h1 {
  margin-top: 20px;
}

.confirmed {
  color: rgb(240, 88, 88);
}

.active {
  color: rgb(107, 107, 219);
}

.recovered {
  color: rgb(45, 167, 45);
}
.deceased {
  color: grey;
}

.confirmed:hover {
  background-color: rgba(235, 163, 163, 0.541);
}

.active:hover {
  background-color: rgba(173, 173, 233, 0.541);
}

.recovered:hover {
  background-color: rgba(134, 235, 134, 0.541);
}

.deceased:hover {
  background-color: rgba(184, 183, 183, 0.534);
}

.state {
  width: 75%;
  margin: 30px auto;
}

.state h1 {
  background-color: #ebe6e68c;
  text-align: center;
  padding: 25px 15px;
  color: #000000;
}

th,
td {
  text-align: left;
  padding: 20px 25px;
  background-color: #ebe6e68c;
  border-radius: 5px;
}

tr {
  margin: 25px;
}

* {
  width: 100%;
  box-sizing: border-box;
}

